import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import * as monaco from 'monaco-editor';
import { EventAPI } from "../../commons/EventAPI";

const dispatchedEvents = [EventAPI.EDITORCODE_CHANGED];

/*
    Example: <srs-codeeditor value="/>
*/
const SrsCodeEditor = function (props, ref) {

    const [editor, setEditor] = useState(null);
    const editorRef = useRef(null);

    let codeValue = (props.value + "") || "";
    let position = props.position || {line: 1, column: 0};
    

    window.MonacoEnvironment = window.MonacoEnvironment || {
        getWorkerUrl: function (moduleId, label) {
            if (label === 'json') {
                return './json.worker.bundle.js';
            }
            if (label === 'css' || label === 'scss' || label === 'less') {
                return './css.worker.bundle.js';
            }
            if (label === 'html' || label === 'handlebars' || label === 'razor') {
                return './html.worker.bundle.js';
            }
            if (label === 'typescript' || label === 'javascript') {
                return './ts.worker.bundle.js';
            }
            return './editor.worker.bundle.js';
        }
    };

    const getCompletionItems = () => {
        // Add code for building a content tree of components and its properties
        const components = [{ name: "component1", props: { prop1: "test1", prop2: "red" }, children: [{ name: "component2", props: { prop3: "test3", prop4: "world" } }] }];
        const completionItems = [];
        components.forEach(component => {
            let children = [];
            if (component.children) {
                children = component.children.map(child => {
                    return {
                        label: child.name,
                        kind: monaco.languages.CompletionItemKind.Class,
                        insertText: child.name,
                        documentation: child.props
                    }
                });
            }
            completionItems.push({
                label: component.name,
                kind: monaco.languages.CompletionItemKind.Class,
                insertText: component.name,
                documentation: component.props,
                children: children
            });
        });
    }

    const dispatchEditorCode = () => {
        props.dispatchEvent(EventAPI.EDITORCODE_CHANGED, { payload: { id: props.id || props.elementid, value: codeValue, position } });
    }

    useEffect(() => {
        if(props.shadowRoot) {
            props.shadowRoot.addEventListener("blur", dispatchEditorCode, { capture: true, composed: true })
        }
        return () => {
            if(props.shadowRoot) {
                props.shadowRoot.removeEventListener("blur", dispatchEditorCode);
            }
        }
    }, []);

    useEffect(() => {
        if (ref.current) {

            let theme = "vs-dark";
            if (props.theme == "white" || props.theme == "g10") {
                theme = "vs"
            } else if (props.theme == "g90" || props.theme == "g100") {
                theme = "vs-dark";
            }


            // Copy over editor styles
            const style = document.querySelector(
                "link[rel='stylesheet'][data-name='vs/editor/editor.main']"
            );
            ref.current.appendChild(style.cloneNode(true));
            let containerEl = ref.current.querySelector("#container");
            if (!editor) {
                const workerUrl = URL.createObjectURL(new Blob([`
                    self.MonacoEnvironment = {
                        baseUrl: '/monaco-editor/min',
                        paths: {
                            vs: '/monaco-editor/min/vs'
                        },
                        getWorkerUrl: function (moduleId, label) {
                            if (label === 'json') {
                                return './language/json/jsonWorker.js';
                            }
                            if (label === 'css' || label === 'scss' || label === 'less') {
                                return './language/css/cssWorker.js';
                            }
                            if (label === 'html' || label === 'handlebars' || label === 'razor') {
                                return './language/html/htmlWorker.js';
                            }
                            if (label === 'typescript' || label === 'javascript') {
                                return './language/typescript/tsWorker.js';
                            }
                            return '../base/worker/workerMain.js';
                        }
                    };
                `], { type: 'text/javascript' }));

                monaco.languages.json.jsonDefaults.setModeConfiguration({
                    ...monaco.languages.json.jsonDefaults.modeConfiguration,
                    worker: {
                        id: 'json',
                        label: 'json',
                        src: workerUrl
                    }
                });
                monaco.languages.json.jsonDefaults.setDiagnosticsOptions({
                    noSemanticValidation: true,
                    noSyntaxValidation: true,
                });

                monaco.languages.css.cssDefaults.setModeConfiguration({
                    ...monaco.languages.css.cssDefaults.modeConfiguration,
                    worker: {
                        id: 'css',
                        label: 'css',
                        src: workerUrl
                    }
                });
                monaco.languages.css.cssDefaults.setDiagnosticsOptions({
                    noSemanticValidation: true,
                    noSyntaxValidation: true,
                });

                monaco.languages.typescript.javascriptDefaults.setCompilerOptions({
                    target: monaco.languages.typescript.ScriptTarget.ES2015,
                    allowNonTsExtensions: true
                });
                monaco.languages.typescript.javascriptDefaults.setDiagnosticsOptions({
                    noSemanticValidation: true,
                    noSyntaxValidation: true,
                });

                monaco.languages.typescript.javascriptDefaults.setWorkerOptions({
                    ...monaco.languages.typescript.javascriptDefaults.workerOptions,
                    module: "commonjs",
                    worker: {
                        id: 'js',
                        label: 'js',
                        src: workerUrl
                    }
                });

                // Set the mode configuration for JavaScript
                monaco.languages.typescript.javascriptDefaults.setEagerModelSync(true);

                monaco.languages.html.htmlDefaults.setModeConfiguration({
                    ...monaco.languages.html.htmlDefaults.modeConfiguration,
                    worker: {
                        id: 'html',
                        label: 'html',
                        src: workerUrl
                    }
                });

                let editorNode = monaco.editor.create(containerEl, {
                    automaticLayout: true,
                    language: "json" || props.language,
                    value: props.value,
                    position: props.position,
                    scrollBeyondLastLine: false,
                    minimap: true,
                    theme: theme,
                    suggest: {
                        triggerCharacters: ['.', ':'],
                        completionItems: getCompletionItems()
                    },
                    quickSuggestions: false, // Disable quick suggestions
                    wordBasedSuggestions: false, // Disable word-based suggestions
                    diagnostics: false // Disable diagnostics
                });
                    
                editorNode.getModel().onDidChangeContent(() => {
                    codeValue = editorNode.getValue();
                    position = editorNode.getPosition();
                    // props.dispatchEvent(EventAPI.EDITORCODE_CHANGED, { payload: { id: props.id || props.elementid, value: value, position } });
                });

                // Add toolbar buttons
                editorNode.addAction({
                    id: "collapse-all",
                    label: "Collapse All",
                    keybindings: [monaco.KeyMod.CtrlCmd | monaco.KeyCode.US_MINUS],
                    run: function (ed) {
                        ed.getAction("editor.foldAll").run();
                    }
                });

                editorNode.addAction({
                    id: "expand-all",
                    label: "Expand All",
                    keybindings: [monaco.KeyMod.CtrlCmd | monaco.KeyCode.US_EQUAL],
                    run: function (ed) {
                        ed.getAction("editor.unfoldAll").run();
                    }
                });
                editorNode.addAction({
                    id: "full-screen",
                    label: "Full Screen",
                    keybindings: [monaco.KeyMod.CtrlCmd | monaco.KeyCode.KEY_F],
                    run: function (ed) {
                        const container = ed.getDomNode();
                        if (container.requestFullscreen) {
                            container.requestFullscreen();
                        } else if (container.msRequestFullscreen) {
                            container.msRequestFullscreen();
                        } else if (container.mozRequestFullScreen) {
                            container.mozRequestFullScreen();
                        } else if (container.webkitRequestFullscreen) {
                            container.webkitRequestFullscreen(Element.ALLOW_KEYBOARD_INPUT);
                        }
                    }
                });

                editorNode.addAction({
                    id: "exit-full-screen",
                    label: "Exit Full Screen",
                    keybindings: [monaco.KeyMod.CtrlCmd | monaco.KeyCode.Escape],
                    run: function (ed) {
                        if (document.exitFullscreen) {
                            document.exitFullscreen();
                        } else if (document.msExitFullscreen) {
                            document.msExitFullscreen();
                        } else if (document.mozCancelFullScreen) {
                            document.mozCancelFullScreen();
                        } else if (document.webkitExitFullscreen) {
                            document.webkitExitFullscreen();
                        }
                    }
                });

                setEditor(editorNode);

            } else {
                if(editor.getValue() != props.value) {
                    // editor.setTheme(props.theme);
                    editor.setValue(props.value);
                    // editor.setLanguage(props.language);
                }
            }
        }

    }, [props.theme, props.value, props.language]);

    return (<>
        {props.label ? <label class="cds--label" style={{ margin: "5px 0px" }}>{props.label}</label> : null}
        <div ref={ref} style={{ width: props.width, height: props.height, border: "1px solid lightgrey", padding: "0px 5px", margin: "5px 0px", borderRadius: 5, backgroundColor: "var(--cds-field)" }}>
            <div id="container" ref={editorRef}></div>
        </div>
    </>);
}

const styles = `
    #container {
        overflow: hidden; width: 100%; height: 100%;
    }
    .monaco-editor, .decorationsOverviewRuler, .minimap {
        --vscode-editor-background: var(--cds-field) !important;
    }
    .monaco-editor .margin {
        --vscode-editorGutter-background: var(--cds-field) !important;
    }
`
const props = [{
    name: "label",
    defaultValue: "",
    type: "string"
}, {
    name: "value",
    defaultValue: "",
    type: "string"
}, {
    name: "language",
    defaultValue: "javascript",
    type: "string"
}, {
    name: "height",
    defaultValue: "200px",
    type: "string"
}, {
    name: "width",
    defaultValue: "40vw",
    type: "string"
}, {
    name: "position",
    defaultValue: {"line": 1, "column": 0},
    type: "object"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsCodeEditor), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };